body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.layout{
  height: 100vh;
  
}

header,
footer{
  background-color: rgb(23, 49, 72);
  color: azure;
}

header{
  height: 5%;
}
.home{
  width:100%;
  background-repeat: no-repeat;
  background-size: 50%;
  background-position: center;
  position: relative;
}

.about{
  width:100%;
  background-repeat: no-repeat;
  background-size: 50%;
  background-position: center;
  position: relative;
}

.title{
  background-image: url("../assests/poke-title.png");
  background-size: 50%;
  width:100%;
  height:100%;
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;               
  top: 50%;                         
  transform: translate(0, -50%)
}
.ball{
  background-image: url("../assests/pokeball.png");
  background-size: 50%;
  width:100%;
  height:100%;
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;               
  top: 50%;                         
  transform: translate(0, -50%)
}

footer{
  height: 15%;
  display: flex;
  justify-content: center;
  align-items: center;
}

ul {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
a{
  text-decoration: none;
}

ul a {
  color: azure;
  border-radius: 2px;
  padding: 2px;
}

ul a:hover{
  background-color: coral;
  
}

main{
  background-color: rgb(51, 90, 121);
  min-height: 80%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  color: azure;
  position: relative;
}

.single-poke{
  color: rgb(119, 80, 2);
}


/*loading*/
.lds-grid {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-grid div {
  position: absolute;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #fff;
  animation: lds-grid 1.2s linear infinite;
}
.lds-grid div:nth-child(1) {
  top: 8px;
  left: 8px;
  animation-delay: 0s;
}
.lds-grid div:nth-child(2) {
  top: 8px;
  left: 32px;
  animation-delay: -0.4s;
}
.lds-grid div:nth-child(3) {
  top: 8px;
  left: 56px;
  animation-delay: -0.8s;
}
.lds-grid div:nth-child(4) {
  top: 32px;
  left: 8px;
  animation-delay: -0.4s;
}
.lds-grid div:nth-child(5) {
  top: 32px;
  left: 32px;
  animation-delay: -0.8s;
}
.lds-grid div:nth-child(6) {
  top: 32px;
  left: 56px;
  animation-delay: -1.2s;
}
.lds-grid div:nth-child(7) {
  top: 56px;
  left: 8px;
  animation-delay: -0.8s;
}
.lds-grid div:nth-child(8) {
  top: 56px;
  left: 32px;
  animation-delay: -1.2s;
}
.lds-grid div:nth-child(9) {
  top: 56px;
  left: 56px;
  animation-delay: -1.6s;
}
@keyframes lds-grid {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.poke-card-content{
  background-image: linear-gradient(to right bottom, #c2417c,#f18ab8, #dfb8ef, #c2417c,#f18ab8, #dfb8ef,  #c2417c,#f18ab8, #dfb8ef)
}
