.poke-container{
    background-color: aqua;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-end;
    align-self: center;

    position: absolute;
    left: 50%;
    transform: translate(-50%);

    min-width: 280px;
    min-height: 280px;
    width: 50vw;
    height: 70vw;

    background-size: cover;
    background-repeat: no-repeat;

}
